<template>
  <div @click="itemClick">
      <el-card class="e_card">
        <span class="span2" >{{ goodsItem.title }}</span>
        <br>
        <span class="span3">{{ goodsItem.content.substr(0, 19) }}...</span>
        <br>
        <div class="e_card_b">
          <span>初声</span>
          <span style="padding-left: 10px">{{ goodsItem.id }} 收藏</span>
          <span style="float: right">{{ goodsItem.updatedAt.substr(5, 6) }}</span>
        </div>
      </el-card>
  </div>
</template>

<script>
export default {
  name: "GoodsListItem",
  data() {
    return {}
  },
  created(){
    var re1 = new RegExp("<.+?>","g");//匹配html标签的正则表达式，"g"是搜索匹配多个符合的内容
    this.goodsItem.content= this.goodsItem.content
      .replace(re1,'')
      .replace(/(<br\s?\/?>)+/gi, '$1')        //去掉重复换行
      .replace(/^<br\/>|<br>/g,'');        //去掉开头的换行;//执行替换成空字符
  },
  beforeUpdate() {
    var re1 = new RegExp("<.+?>","g");//匹配html标签的正则表达式，"g"是搜索匹配多个符合的内容
    this.goodsItem.content= this.goodsItem.content
      .replace(re1,'')
      .replace(/(<br\s?\/?>)+/gi, '$1')        //去掉重复换行
      .replace(/^<br\/>|<br>/g,'');        //去掉开头的换行;//执行替换成空字符
  },
  methods: {
    itemClick(){
      this.$router.push('/details/' + this.goodsItem.id)
    }
  },
  props:{
    goodsItem:{
      type: Object,
      default(){
        return {}
      }
    }
  }
}
</script>

